import {useState, useEffect, useRef} from "react"
import "./Portfolio.css"
import Project from "./Project"
import Loading from "./Loading"
import Navigation from "./Navigation"

export default function Portfolio(){

    const [projects, setProjects] = useState(null)
    const [filteredProjects, setFilteredProjects] = useState(null)
    
    useEffect(()=>{
        fetch(`${process.env.PUBLIC_URL}/projects.json`)
        .then(res => res.json())
        .then(data =>{
            setProjects(data)
            setFilteredProjects(shuffle(data))
        })
    },[])

    const listRef = useRef();
    let projectsList = <Loading />
    if(filteredProjects != null){
        projectsList = filteredProjects.map((project,index) =>{
            return(
                <Project title={project.title} description={project.description} img={project.img} link={project.link} linkCode={project.linkCode} tech={project.tech} platforms={project.platforms} key={index}/>
            )
        })
    }
    
    function filterCategory(category){
        setFilteredProjects(shuffle(projects.filter(project => project.category === category)))
        listRef.current.classList.remove("portfolio-animation-enter")
        listRef.current.classList.add("portfolio-animation-exit")
        setTimeout(() => {
            listRef.current.classList.add("portfolio-animation-enter")
            listRef.current.classList.remove("portfolio-animation-exit")
        }, 250);
    }

    function shuffle(array) {
        let currentIndex = array.length, temporaryValue, randomIndex;
        while (0 !== currentIndex) {
          randomIndex = Math.floor(Math.random() * currentIndex);
          currentIndex -= 1;
          temporaryValue = array[currentIndex];
          array[currentIndex] = array[randomIndex];
          array[randomIndex] = temporaryValue;
        }
        return array;
      }

    return(
        <div className="container mb-2">
            <Navigation click={filterCategory}/>
            <div className="row portfolio-animation-enter" ref={listRef}>
                {projectsList}
            </div>
        </div>
    )
}