import "./Navigation.css"

export default function Navigation({click}){
    return(
        <div className="navigation text-center m-2 navigation-animation-enter">
            <div className="btn-group" role="group" aria-label="Basic example">
                <button type="button" className="btn btn-dark" onClick={() => click("web")}><i className="fa fa-globe" aria-hidden="true"></i> Web Apps</button>
                <button type="button" className="btn btn-dark" onClick={() => click("mobile")}><i className="fa fa-mobile" aria-hidden="true"></i> Mobile Apps</button>
                <button type="button" className="btn btn-dark" onClick={() => click("games")}><i className="fa fa-gamepad" aria-hidden="true"></i> Games</button>
            </div>
        </div>
    )
}